import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import { makeStyles, Theme, createStyles, Typography } from '@material-ui/core';
import Emoji from '../Utils/Emoji';
import AnimatedAngleIcon from '../Animated/AnimatedAngle';
import { useScrollDown } from 'src/hooks/useScrollDown';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      height: `100vh`,
    },
    wrapper: {
      position: 'absolute',
      top: '40%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      [theme.breakpoints.down('md')]: {
        top: '30%',
      },
    },
    title: {
      whiteSpace: 'nowrap',
      fontWeight: theme.typography.fontWeightLight,
      '& strong': {
        color: theme.palette.primary.main,
        fontWeight: theme.typography.fontWeightBold,
      },
    },
    description: {
      margin: 0,
      fontWeight: theme.typography.fontWeightMedium,
    },
    bottomArrow: {
      position: 'absolute',
      bottom: '10%',
      left: '50%',
      transform: 'translateX(-50%)',
    },
  })
);

const MentoringJumbotron = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const styles = useStyles();

  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 300,
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 600,
  });
  const bottomArrowAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 900,
  });

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <div ref={containerRef} className={styles.root}>
      <div className={styles.wrapper}>
        <animated.div style={titleAnimation}>
          <Typography variant="h3" component="h1" gutterBottom={true} className={styles.title}>
            안녕하세요.
            <br />
            <strong>루비콘 멘토링 프로젝트</strong>에 오신 것을 환영합니다 <Emoji emoji="👋" name="hi" />
          </Typography>
        </animated.div>
        <animated.div style={descriptionAnimation}>
          <Typography>
            <strong>루비콘 멘토링 프로젝트</strong>는 개발자와 디자이너들이 모인 루비콘 팀의 멤버들이 자신이 쌓아온
            지식과 경험을 다른 사람들에게 공유하고 IT 업계의 생태계에 선한 이펙트를 주고 싶다는 취지에서 출발한
            프로젝트입니다.
            <br />
            수많은 성장의 방향 속에서 길을 잃고 있는 분들에게 조금이나마 도움이 되었으면 합니다.
          </Typography>
        </animated.div>
      </div>
      <div className={styles.bottomArrow}>
        <animated.div style={bottomArrowAnimation}>
          <AnimatedAngleIcon onClick={scrollToBottom} />
        </animated.div>
      </div>
    </div>
  );
};

export default MentoringJumbotron;
