import React, { useRef } from 'react';
import { makeStyles, Typography, Theme, Grid, Container } from '@material-ui/core';
import { graphql, useStaticQuery } from 'gatsby';
import { FluidImageSharp } from 'src/models/image';
import GatsbyImage from 'gatsby-image';
import AnimatedAngleIcon from '../Animated/AnimatedAngle';
import { useScrollDown } from 'src/hooks/useScrollDown';

const useStyles = makeStyles<Theme>(({ spacing, typography }) => ({
  section: {
    position: 'relative',
    height: '100vh',
  },
  header: {
    marginBottom: spacing(3),
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  image: {
    width: 550,
  },
  bottomArrow: {
    position: 'absolute',
    bottom: '10%',
    left: '50%',
    transform: 'translateX(-50%)',
  },
}));

const IntroSection = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const styles = useStyles();
  const { rocketImage } = useStaticQuery<{ rocketImage: FluidImageSharp }>(graphql`
    query {
      rocketImage: file(absolutePath: { regex: "/growth.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const scrollToBottom = useScrollDown(containerRef);

  return (
    <Container>
      <Grid ref={containerRef} container component="section" className={styles.section} alignItems="center">
        <Grid item xs={12} sm>
          <Typography
            variant="h4"
            component="h2"
            gutterBottom={true}
            className={`${styles.whiteSpacePre} ${styles.title}`}
          >
            성장을 통해 나의 가치를 높혀보세요.
          </Typography>
          <Typography>
            나름 열심히 공부하고 있는데 내가 맞는 방향으로 가고 있는건지 궁금하신가요?
            <br />
            좋은 동료들과 피드백을 주고받으며 건설적인 성장을 하고 싶은데, 그런 환경을 찾기가 어려운가요?
            <br />
            도대체 어떻게 하면 좋은 제품을 만들 수 있을까요?
            <br />
            <br />
            여러분이 한번쯤은 고민해 보았던 이런 질문들에 대한 해답을 찾아나서는 여정을 루비콘이 도와드릴게요!
          </Typography>
        </Grid>
        <Grid item xs={12} sm="auto" className={styles.image}>
          <GatsbyImage fluid={rocketImage.childImageSharp.fluid} />
        </Grid>
        <div className={styles.bottomArrow}>
          <AnimatedAngleIcon onClick={scrollToBottom} />
        </div>
      </Grid>
    </Container>
  );
};

export default IntroSection;
